import hljs from 'highlight.js';
import marked from 'marked';
import range from './js/range';
import Print from './js/print';

hljs.initHighlightingOnLoad();

marked.setOptions({
  renderer: new marked.Renderer(),
  gfm: true,
  tables: true,
  breaks: false,
  pedantic: false,
  sanitize: true,
  smartLists: true,
  highlight: function (code) {
    return hljs.highlightAuto(code).value;
  }
});

export default {
  name: 'markdown',
  props: {
    title: { // 
      type: String,
      default: ''
    },
    titleStyle: { // 
      type: Object,
      default () {
        return {}
      }
    },
    theme: { // 
      type: String,
      default: 'Light'
    },
    width: { // 
      type: [Number, String],
      default: 'auto'
    },
    height: { // 
      type: Number,
      default: 600
    }, // 
    toolbars: { // 
      type: Object,
      default () {
        return {};
      }
    },
    autoSave: { // 
      type: Boolean,
      default: true
    },
    interval: { //  
      type: Number,
      default: 10000
    },
    initialValue: { // 
      type: String,
      default: ''
    },
    mode: { //  1  2  3 
      type: [Number, String],
      default: 1
    }
  },
  data() {
    return {
      value: '', // 
      timeoutId: null,
      hljsInit: null,
      indexLenth: 1,
      previewMarkdown: '',
      preview: 1, // 
      isFullscreen: false,
      scrollHeight: null,
      scroll: 'markdown', // 
      allTools: { // 
        strong: true,
        italic: true,
        overline: true,
        h1: true,
        h2: true,
        h3: true,
        h4: false,
        h5: false,
        h6: false,
        hr: true,
        quote: true,
        ul: true,
        ol: true,
        code: true,
        link: true,
        image: true,
        table: true,
        checked: true,
        notChecked: true,
        shift: true,
        fullscreen: true,
        print: false,
        theme: true,
        exportmd: true,
        importmd: true
      },
      slideDown: false,
      themeName: 'Light', // 
      lastInsert: '',
      timerId: null, // id

    };
  },
  computed: {
    editorHeight() {
      if (this.isFullscreen) {
        return window.innerHeight;
      } else {
        return this.height;
      }
    },
    tools() {
      const {
        allTools,
        toolbars
      } = this;
      return Object.assign(allTools, toolbars)
    }
  },
  mounted() {

    this.$nextTick(() => {
      this.$refs.textarea.focus();
    })
    this.init();
    //this.addListener();
  },
  methods: {
    init() {
      this.themeName = this.theme;
      const {
        autoSave,
        interval,
        theme,
        initialValue,
        mode
      } = this;
      this.value = initialValue;
      this.preview = mode;
      this.previewMarkdown = marked(initialValue, {
        sanitize: true
      });
      if (autoSave) {
        this.timerId = setInterval(() => {
          this.handleSave();
        }, interval)
      }

    },
    markdownScroll() {
      if (this.scroll === 'markdown') {
        const markdownContent = this.$refs.markdownContent;
        const preview = this.$refs.preview;
        const markdownScrollHeight = markdownContent.scrollHeight;
        const markdownScrollTop = markdownContent.scrollTop;
        const previewScrollHeight = preview.scrollHeight;
        preview.scrollTop = parseInt((markdownScrollTop / markdownScrollHeight) * previewScrollHeight);
      }
    },
    previewScroll() {
      if (this.scroll === 'preview') {
        const markdownContent = this.$refs.markdownContent;
        const preview = this.$refs.preview;
        const markdownScrollHeight = markdownContent.scrollHeight;
        const previewScrollHeight = preview.scrollHeight;
        const previewScrollTop = preview.scrollTop;
        markdownContent.scrollTop = parseInt((previewScrollTop / previewScrollHeight) * markdownScrollHeight);
      }
    },
    mousescrollSide(side) { // 
      this.scroll = side;
    },
    insertContent(str) { // 
      const {
        preview
      } = this;
      if (preview === 2) {
        return;
      }
      this.lastInsert = str;
      const textareaDom = this.$refs.textarea;
      const point = this.getCursortPosition();
      const lastChart = this.value.substring(point - 1, point);
      const lastFourCharts = this.value.substring(point - 4, point);
      if (lastChart != '\n' && this.value != '' && lastFourCharts != '    ') {
        str = '\n' + str;
        this.insertAfterText(str);
      } else {
        this.insertAfterText(str);
      }
    },
    getCursortPosition() { // 
      const textDom = this.$refs.textarea;
      let cursorPos = 0;
      if (document.selection) {
        textDom.focus();
        let selectRange = document.selection.createRange();
        selectRange.moveStart('character', -this.value.length);
        cursorPos = selectRange.text.length;
      } else if (textDom.selectionStart || textDom.selectionStart == '0') {
        cursorPos = textDom.selectionStart;
      }
      return cursorPos;
    },
    insertAfterText(value) { // 
      const textDom = this.$refs.textarea;
      let selectRange;
      if (document.selection) {
        textDom.focus();
        selectRange = document.selection.createRange();
        selectRange.text = value;
        textDom.focus();
      } else if (textDom.selectionStart || textDom.selectionStart == '0') {
        const startPos = textDom.selectionStart;
        const endPos = textDom.selectionEnd;
        const scrollTop = textDom.scrollTop;
        textDom.value = textDom.value.substring(0, startPos) + value + textDom.value.substring(endPos, textDom.value.length);
        textDom.focus();
        textDom.selectionStart = startPos + value.length;
        textDom.selectionEnd = startPos + value.length;
        textDom.scrollTop = scrollTop;
      } else {
        textDom.value += value;
        textDom.focus();
      }
      this.$set(this, 'value', textDom.value);
    },
    setCaretPosition(position) { // 
      const textDom = this.$refs.textarea;
      if (textDom.setSelectionRange) {
        textDom.focus();
        textDom.setSelectionRange(position, position);
      } else if (textDom.createTextRange) {
        var range = textDom.createTextRange();
        range.collapse(true);
        range.moveEnd('character', position);
        range.moveStart('character', position);
        range.select();
      }
    },
    insertLine() { // 
      this.insertContent(`\n----\n`);
    },
    insertTitle(level) { // 
      const titleLevel = {
        1: '\n#  ',
        2: '\n##  ',
        3: '\n###  ',
        4: '\n####  ',
        5: '\n#####  ',
        6: '\n######  '
      };
      this.insertContent(titleLevel[level]);
    },
    insertQuote() { // 
      this.insertContent('\n>  ');
    },
    insertUl() { // 
      this.insertContent('-  ');
    },
    insertOl() { // 
      this.insertContent('1. ');
    },
    insertFinished() { // 
      this.insertContent('- [x]  ');
    },
    insertNotFinished() { // 
      this.insertContent('- [ ]  ');
    },
    insertLink() { // 
      this.insertContent('\n[](https://github.com/coinsuper)');
    },
    insertImage() { // 
      this.insertContent('\n![image](https://noticejs.oss-cn-hangzhou.aliyuncs.com/%E6%9C%AA%E6%A0%87%E9%A2%98-3.jpg)');
    },
    insertTable() { // 
      this.insertContent(`\nheader 1 | header 2\n---|---\nrow 1 col 1 | row 1 col 2\nrow 2 col 1 | row 2 col 2\n\n`);
    },
    insertCode() { // code
      const textareaDom = this.$refs.textarea;
      const point = this.getCursortPosition();
      const lastChart = this.value.substring(point - 1, point);
      this.insertContent('\n```\n\n```');
      if (lastChart != '\n' && this.value != '') {
        this.setCaretPosition(point + 5);
      } else {
        this.setCaretPosition(point + 5);
      }
    },
    insertStrong() { // 
      const textareaDom = this.$refs.textarea;
      const point = this.getCursortPosition();
      const lastChart = this.value.substring(point - 1, point);
      this.insertContent('****');
      if (lastChart != '\n' && this.value != '') {
        this.setCaretPosition(point + 2);
      } else {
        this.setCaretPosition(point + 2);
      }
    },
    insertItalic() { // 
      const textareaDom = this.$refs.textarea;
      const point = this.getCursortPosition();
      const lastChart = this.value.substring(point - 1, point);
      this.insertContent('**');
      if (lastChart != '\n' && this.value != '') {
        this.setCaretPosition(point + 1);
      } else {
        this.setCaretPosition(point + 1);
      }
    },
    insertBg() { // 
      const textareaDom = this.$refs.textarea;
      const point = this.getCursortPosition();
      const lastChart = this.value.substring(point - 1, point);
      this.insertContent('====');
      if (lastChart != '\n' && this.value != '') {
        this.setCaretPosition(point + 5);
      } else {
        this.setCaretPosition(point + 5);
      }
    },
    insertUnderline() { // 
      const textareaDom = this.$refs.textarea;
      const point = this.getCursortPosition();
      const lastChart = this.value.substring(point - 1, point);
      this.insertContent('<u></u>');
      if (lastChart != '\n' && this.value != '') {
        this.setCaretPosition(point + 3);
      } else {
        this.setCaretPosition(point + 5);
      }
    },
    insertOverline() { // overline
      const textareaDom = this.$refs.textarea;
      const point = this.getCursortPosition();
      const lastChart = this.value.substring(point - 1, point);
      this.insertContent('~~~~');
      if (lastChart != '\n' && this.value != '') {
        this.setCaretPosition(point + 2);
      } else {
        this.setCaretPosition(point + 2);
      }
    },
    insertTitle(level) { // 
      const titleLevel = {
        1: '#  ',
        2: '##  ',
        3: '###  ',
        4: '####  ',
        5: '#####  ',
        6: '######  '
      };
      this.insertContent(titleLevel[level]);
    },
    save(e) { // ctrl+s 
      e.preventDefault();
      this.handleSave();
    },
    tab(e) { // teatarea tab
      this.insertContent('    ', this);
      if (e.preventDefault) {
        e.preventDefault();
      } else {
        e.returnValue = false;
      }
    },
    handleSave() { // 
      this.$emit('on-save', {
        markdownValue: this.value,
        htmlValue: this.previewMarkdown,
        theme: this.theme
      });
    },
    insertLine() { // 
      this.insertContent(`\n----\n`);
    },
    toggleSlideDown() { // 
      this.slideDown = !this.slideDown;
    },
    setThemes(name) { // 
      this.themeName = name;
      this.slideDown = false;
    },
    enter(e) { // 
      const {
        lastInsert
      } = this;
      const list = ['-  ', '1. ', '- [ ]  ', '- [x]  ']
      if (list.includes(lastInsert)) {
        this.insertContent(lastInsert);
      }
    },
    onDelete() { // ,''
      const lines = this.value.split('\n');
      if (lines[lines.length - 1] == '') {
        this.lastInsert = '';
      }
    },
    print() { // 
      const dom = this.$refs.preview;
      Print(dom);
    },
    addListener() { // 
      this.removeListener();
      document.addEventListener('keydown', this.listener)
    },
    removeListener() {
      document.removeEventListener('keydown', this.listener)
    },
    listener(e) {
      if (e.keyCode === 83) {
        if (e.metaKey || e.ctrlKey) {
          e.preventDefault();
          this.handleSave();
        }
      }
    },
    importFile(e) { // 
      const file = e.target.files[0];
      if (!file) {
        return;
      }
      const {
        type
      } = file;
      if (type !== 'text/markdown') {
        alert('');
        return;
      }
      const reader = new FileReader();
      reader.readAsText(file, {
        encoding: 'utf-8'
      });
      reader.onload = () => {
        this.value = reader.result;
        e.target.value = '';
      }
    },
    exportMd() { // 
      const {
        value
      } = this;
      var pom = document.createElement('a');
      pom.setAttribute('href', 'data:text/plain;charset=UTF-8,' + encodeURIComponent(value));
      pom.setAttribute('download', '.md');
      pom.style.display = 'none';
      if (document.createEvent) {
        var event = document.createEvent('MouseEvents');
        event.initEvent('click', true, true);
        pom.dispatchEvent(event);
      } else {
        pom.click();
      }
    }
  },
  watch: {
    value() {
      clearTimeout(this.timeoutId);
      this.timeoutId = setTimeout(() => {
        this.previewMarkdown = marked(this.value, {
          sanitize: true
        });
      }, 30)
      this.indexLenth = this.value.split('\n').length;
      const height_1 = this.indexLenth * 22;
      const height_2 = this.$refs.textarea.scrollHeight;
      const height_3 = this.$refs.preview.scrollHeight;
      this.scrollHeight = Math.max(height_1, height_2, height_3);
    }
  },
  destroyed() { // 
    clearInterval(this.timerId);
  }
};



